.dashboardContainer{display:flex;height:100vh}.sidebar{background-color:#2c3e50;color:#ecf0f1;transition:width .3s ease;overflow:hidden}.sidebar.sidebarOpen{width:250px}.sidebar.sidebarClosed{width:70px}.sidebar .hidden{opacity:0;pointer-events:none}.sidebar .sidebarHeader{display:flex;justify-content:space-between;align-items:center;padding:10px}.sidebar .sidebarHeader .logo{font-size:1.5rem;font-weight:bold}.sidebar .navMenu ul{list-style:none;padding:0}.sidebar .navMenu ul li{margin:10px 0}.sidebar .navMenu ul li .navItem{display:flex;align-items:center;padding:10px;text-decoration:none;color:inherit;transition:background-color .3s}.sidebar .navMenu ul li .navItem:hover{background-color:#34495e}.sidebar .navMenu ul li .navItem svg{margin-right:10px}.mainContent{flex:1;display:flex;flex-direction:column}.mainContent .header{background-color:#ecf0f1;display:flex;justify-content:space-between;align-items:center;padding:10px}.mainContent .header .searchBar{display:flex;align-items:center;background-color:#bdc3c7;border-radius:4px;padding:5px}.mainContent .header .searchBar .searchInput{border:none;background:rgba(0,0,0,0);outline:none;margin-left:5px}.mainContent .header .headerActions{display:flex;align-items:center}.mainContent .header .headerActions .iconButton{margin-left:10px}.mainContent .contentArea{padding:20px}